/**
 * 事件中心详情 cx
 */
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { EventDefinitionBean } from '../bean/event-definition.bean';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
@Component({
	selector: 'eventcenter-activity-detail',
	templateUrl: './eventcenter-activity-detail.component.html',
	styleUrls: ['./eventcenter-activity-detail.component.scss']
})
export class EventcenterActivityDetail implements OnInit {
	eventDefinitionBean: EventDefinitionBean = new EventDefinitionBean;
	triggerWay: string;
	setInValue: any = {
		timeName: '',
	};
	//提示信息
	msgs: Message[] = [];
	ruleList: any = [];
	constructor(
		public commfunc: Commfunc,
		public cusOperationHttpService: CusOperationHttpService,
		public router: Router
	) { }

	ngOnInit() {
		this.eventDefinitionBean = JSON.parse(this.commfunc.getSessionDataCH('detail_ec_eventDefinitionBean'));
		this.queryEventResponseRuleList(this.eventDefinitionBean)
		// eventId
		this.codeValue.codeValues() //调用方法，获取全部码
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.codeValue.eventTypeOptions = this.codeValue.code['eceventTypeList'];	// 事件类型
		this.codeValue.responseTypeOptions = this.codeValue.code['ecresponseTypeOptions'];	// 响应类型

	}

	//返回按钮
	returnBtn() {
		this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/eventcenter-activity-query'])
	}

	// 事件规则查询
	queryEventResponseRuleList(param: any) {
		this.cusOperationHttpService.queryEventResponseRuleList(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				data.eventResponseRuleList.forEach((item, index) => {
					item['sendChannel'] = '小微智慧金融服务平台';
					if (item.responseType == '1') {
						item['serviceTemplateName'] = item.execServiceName;
						item['methodContent'] = item.execFunction;
					}else if (item.responseType == '2' || item.responseType == '3') {
						item['serviceTemplateName'] = item.messageStencilName;
						item['methodContent'] = item.messageContent;
					}
				});
				console.log(data.eventResponseRuleList);
				this.ruleList = data.eventResponseRuleList;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		});
	}

	// 码值
	codeValue = {
		commfunc: this.commfunc,
		code: [],
		// 事件类型
		eventTypeOptions: [],
		// 响应类型
		responseTypeOptions: [],
		// 触发方式
		triggerWayOptions: [
			{ label: '外部触发', value: '2' },
			{ label: '时间触发', value: '1' }
		],
		// 事件级别
		eventLevels: [
			{ label: '一般事件', value: '1' },
			{ label: '弱事件', value: '2' },
			{ label: '强事件', value: '3' }
		],
		// 事件来源
		eventSourceLsit: [
			{ label: '客户自定义', value: '1' },
			{ label: '用户自定义', value: '2' },
			{ label: '周期性行为', value: '3' },
			{ label: '短信抓取（我行）', value: '4' },
			{ label: '短信抓取（他行）', value: '5' },
			{ label: '枢纽推送', value: '6' },
			{ label: '其他', value: '7' }
		],
		// 来源渠道
		sourceChannelList: [
			{ label: '24小时营业厅', value: '1' },
			{ label: '小微智慧金融服务平台', value: '2' },
			{ label: '移动工作站', value: '3' },
			{ label: '移动审批', value: '4' },
			{ label: '短信', value: '5' },
			{ label: '微信', value: '6' }
		],
		// 执行周期单位
		executionCycleUnitOptions: [
			{ label: '天', value: '4' },
			{ label: '周', value: '5' },
			{ label: '月', value: '6' },
			{ label: '年', value: '7' }
		],
		// 处理模式 1、全部客户搜索 2、数据变动处理 3、业务交易处理 4、自定义指定
		processingModeOptions: [
			{ label: '全部客户搜索', value: '1' },
			{ label: '数据变动处理', value: '2' },
			{ label: '业务交易处理', value: '3' },
			{ label: '自定义指定', value: '4' }
		],
		// 对象类型
		objectTypeOptions: [
			{ label: '银行客户', value: '1' },
			{ label: '客户经理', value: '2' }
		],
		// 处理服务
		searchServiceOptions: [
			{ label: '高级搜索', value: '1' },
			{ label: '流式', value: '2' }
		],

		//获取码值
		codeValues() {
			this.commfunc.codeValue();
			let ret = this.commfunc.codeDatas;
			return this.code = ret //code是自己声明的变量
		}
	}
}
