
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';


import { EventDefinitionBean } from 'app/pages/tzb/custom/eventcenter/eventcenter-activite/bean/event-definition.bean';
import { EventRespRuleBean } from 'app/pages/tzb/custom/eventcenter/eventcenter-activite/bean/event-resp-rule.bean';

// import { functionTypes, configLevel, temporary,select_1,select_2  } from './../../../constant/codeValue';
import { StepsModule, MenuItem, Message, SelectItem } from 'primeng/primeng';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'ecenter-view-detail',
  templateUrl: './ecenter-view-detail.component.html',
  styleUrls: ['./ecenter-view-detail.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
/**
 * 可售详情
 */
export class EcenterViewDetailComponent implements OnInit, OnChanges {
  vlaueOpen: string = 'open';
  

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //实体
  eventDefinitionBean: EventDefinitionBean = new EventDefinitionBean();
  eventRespRuleBean: EventRespRuleBean = new EventRespRuleBean();
  //列表
  cars_2: any;
  list: any = [];
  listPack: any = [];
  //基本信息
  listMsgs: any = [];
  //提示信息
  tabMesg: any = tableMessage;
  msgs: Message[] = [];
  zujian_ist1: any = [];
  shuxing_ist2: any = [];
  aIndex: number = 0;
  i: number;
  showClass: string;//点击组件
  cars_right: any = [];//属性列表
  cars_attrbute: any = [];//属性列表
  configLevelV: any;//属性配置级别
  functionTypeV: any;//功能类型
  code: any;
  //产品营销
  val1: string = '';//产品说明书
  val2: string = '';//产品购买协议
  val3: string = '';//操作手册
  files21: any = [];//产品说明书
  files22: any = [];//产品购买协议
  files23: any = [];//操作手册
  files24: any = [];//视频
  managememntactive: string = '';//营销活动
  managememntspeech: string = '';//营销话术
  productMarketing: string = '';//产品说明书
  productshopping: string = '';//产品购买协议
  operationmanual: string = '';//操作手册
  operationmanual2: any = [];//操作手册
  productshopping2: any = [];//产品购买协议
  productMarketing2: any = [];//产品说明书
  vedio: any = [];//视频
  fileList24: any = [];//视频
  fileList23: any = [];//操作手册
  fileList22: any = [];//产品购买协议
  fileList21: any = [];//产品说明书
  productMarketingList: any = [];//产品说明书

  //包装
  zujian1: any = [];
  zujian2: any = [];
  zujian3: any = [];
  zujian4: any = [];
  zujian5: any = [];
  zujian6: any = [];
  zujian7: any = [];
  zujian8: any = [];
  zujian9: any = [];

  items1: any = [];
  items2: any = [];

  zujian_ist2: any = [];
  zujian_ist3: any = [];

  files10: any = [];
  files11: any = [];
  files12: any = [];
  files13: any = [];
  files14: any = [];
  files15: any = [];
  files16: any = [];
  files17: any = [];
  files18: any = [];

  //基本信息
  introductionDate: any;//引入日期（格式yyyy-MM-dd HH:mm:ss）
  releaseDate: any;//截止日期，（格式yyyy-MM-dd）
  productCurrency: string = '';
  inOut: string = '';
  crmValue: string = '';
  businessValue: string = '';
  cbusinessValue: string = '';
  saleDepartment: string = '';
  salee: any = [];
  salee1: any = [];
  belong1: any = [];
  belongs1: any = [];
  cfsaleDepartment: string = '';
  productManager: string = '';
  saleUser: string = '';
  special_attr: any = [];
  select_1: any;//属性类型
  select_2: any;//属性类型
  temporary: any;//属性类型
  detaillists: any = [];
  siteList: any = [];
  show: string = '';//默认选中第1个

  createdDate :string = '';

  eceventTypeList: any = [];
  ecsstatusList: any = [];
  displayFlagOptions: any = [];//是否展示
  constructor(public cusOperationHttpService: CusOperationHttpService,
    public route: ActivatedRoute,
    public commfunc: Commfunc, ) {
  

    this.codeValues() //调用方法，获取全部码值

    this.eceventTypeList = this.code['eceventTypeList'];
    this.ecsstatusList = this.code['ecsstatusList'];
    this.functionTypeV = this.code['functionTypes'];
    this.configLevelV = this.code['configLevel'];
    this.select_1 = this.code['select_1'];
    this.select_2 = this.code['select_2'];
    this.temporary = this.code['temporary'];
    this.displayFlagOptions = this.code['DisplayFlag'];
  }

  ngOnInit() {
   
     // this.queryEvent();

      this.eventDefinitionBean = JSON.parse(this.commfunc.getSessionDataCH('detail_ec_eventDefinitionBean'));
      debugger;
  
  }
  ngOnChanges() {
    this.queryEvent();
  }
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
  queryEvent() {
    let param = {} ;
   
      param = {
        productId: this.route.snapshot.params['baseProductId'],
        status: this.route.snapshot.params['status']
      }
   
    this.cusOperationHttpService.getAvailableProduct(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list = data.availableProduct;//详情展示
        this.cars_2 = data.productFunctionList;//功能查询
        this.zujian_ist1 = data.productFeatureCategoryList;//组件展示

        this.shuxing_ist2 = data.productFeatureCategoryList[0].productFeatureTypeList;//属性展示

        this.dianji(0, this.zujian_ist1[0])
        // 基本信息
        this.productCurrency = data.availableProduct.currency;
        this.inOut = data.availableProduct.inOutTableMode;
        this.crmValue = data.availableProduct.crmProductTreeCode;
        this.businessValue = data.availableProduct.businessCode;
        this.cbusinessValue = data.availableProduct.subBusinessCode;
        let market = '', manage = '', product = '', sales = '';
        data.availableProduct.marketDepartmentList.forEach(e => {
          if (data.availableProduct.marketDepartmentList.length > 1) {
            market += e.marketDepartmentName + ',';
          } else {
            market = e.marketDepartmentName;
          }
        });
        this.saleDepartment = market;
        data.availableProduct.manageDepartmentList.forEach(e => {
          if (data.availableProduct.manageDepartmentList.length > 1) {
            manage += e.manageDepartmentName + ',';
          } else {
            manage = e.manageDepartmentName;
          }
        });
        this.cfsaleDepartment = manage;
        data.availableProduct.productManagerList.forEach(e => {
          if (data.availableProduct.productManagerList.length > 1) {
            product += e.productManagerName + ',';
          } else {
            product = e.productManagerName;
          }
        });
        this.productManager = product;
        data.availableProduct.salesPostList.forEach(e => {
          if (data.availableProduct.salesPostList.length > 1) {
            sales += e.salesPostName + ',';
          } else {
            sales = e.salesPostName;
          }
        });
        this.saleUser = sales;
        this.releaseDate = new Date(this.list.releaseDate);
        this.introductionDate = new Date(this.list.introductionDate);
        this.releaseDate = this.releaseDate.toLocaleDateString();
        this.introductionDate = this.introductionDate.toLocaleDateString();
        if (data.productMsgs.length > 0) {
          this.listMsgs = data.productMsgs;
          this.listMsgs.forEach(items => {
            if (items.attrType == 'crmNum') {
              this.crmValue = items.attrValue;
            } else if (items.attrType == 'cateNum') {
              this.businessValue = items.attrValue;
            } else if (items.attrType == 'subCateNum') {
              this.cbusinessValue = items.attrValue;
            } else if (items.attrType == 'salePerson') {
              this.salee = [];
              items.attrValue.split(',').forEach(itemse => {
                this.salee.push(itemse.split('#')[0]);
              });
              this.saleDepartment = this.salee.join(',');
            } else if (items.attrType == 'manageDept') {
              this.salee1 = [];
              items.attrValue.split(',').forEach(itemse => {
                this.salee1.push(itemse.split('#')[0]);
              });
              this.cfsaleDepartment = this.salee1.join(',');
            } else if (items.attrType == 'salesman') {
              this.belong1 = [];
              items.attrValue.split(',').forEach(itemse => {
                this.belong1.push(itemse.split('#')[0]);
              });
              this.saleUser = this.belong1.join(',');
            } else if (items.attrType == 'productManager') {
              this.belongs1 = [];
              items.attrValue.split(',').forEach(itemse => {
                this.belongs1.push(itemse.split('#')[0]);
              });
              this.productManager = this.belongs1.join(',');
            } else if (items.attrType == 'productCurrency') {
              this.productCurrency = items.attrValue;
            } else if (items.attrType == 'inOut') {
              this.inOut = items.attrValue;
              this.inOut = items.attrValue;
            } else if (items.attrType == '0' || items.attrType == '1' || items.attrType == '2') {
              this.special_attr.push(items);
              this.special_attr = _.clone(this.special_attr);
            }
          });
        }
        if (this.special_attr.length == 0) {
          this.special_attr = null;
        }
        //包装
        if (data.productContentList.length > 0) {
          this.detaillists = [];
          this.detaillists = data.productContentList;
          this.siteList = [];
          this.detaillists.forEach(items => {
            this.siteList.push({ siteName: items.channelName, webSiteId: items.channelId });
          });
          let sited = {
            siteName: this.siteList[0].siteName,
            webSiteId: this.siteList[0].webSiteId
          }
          this.show = this.siteList[0].siteName;
          this.siteClick(sited);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  dianji(i, item) {
    this.showClass = item.productFeatureCategoryName;
    let arrayN = []
    this.aIndex = i;
    this.shuxing_ist2 = this.zujian_ist1[i].productFeatureTypeList;
    this.shuxing_ist2.forEach(item => {
      if (item.featureType == '01') {
        item.shuxingType = 1;
        item.productFeatureList.forEach(items => {
          arrayN.push(items.productFeatureName)
        })
        item.shuxingmiaoshu = arrayN.join(",")
        arrayN = []
      }
      if (item.featureType == '02') {
        item.shuxingType = 2;
        item.productFeatureList.forEach(items => {
          if (items.valueType == '05') {
            item.shuxingType = 3;
            item.def = items.def;
          } else if (items.valueType == '06') {
            item.shuxingType = 61;
            item.def = items.def;
          } else {
            item.min = items.min;
            item.max = items.max;
          }
        })
      }
    });
  }
  //包装点击
  siteClick(col) {
    this.detaillists.forEach(items => {
      if (items.channelId == col.webSiteId) {
        this.listPack = items;
        //产品描述
        let counted1 = 0;
        let counted2 = 0;
        let counted3 = 0;
        this.zujian1 = '';
        this.zujian2 = '';
        this.zujian3 = '';
        this.zujian4 = '';
        this.zujian5 = '';
        this.zujian6 = '';
        this.zujian7 = [];
        this.zujian8 = [];
        this.zujian9 = [];
        this.listPack.productTextList.forEach(item => {
          if (item.dataResourceName == 'systemSummary') {
            this.zujian1 = item.objectInfo;
          } else if (item.dataResourceName == 'marketScript') {
            this.zujian2 = item.objectInfo;
          } else if (item.dataResourceName == 'termDesc') {
            this.zujian3 = item.objectInfo;
          } else if (item.dataResourceName == 'limitDesc') {
            this.zujian4 = item.objectInfo;
          } else if (item.dataResourceName == 'sameProductlnformation') {
            this.zujian5 = item.objectInfo;
          } else if (item.dataResourceName == 'others') {
            this.zujian6 = item.objectInfo;
          } else if (item.dataResourceName == 'listFeature') {
            this.zujian7 = item.objectInfo.split(',');
            counted1++;
          } else if (item.dataResourceName == 'productDetail') {
            this.items1 = item.objectInfo.split(',');
            counted2++;
          } else if (item.dataResourceName == 'productPhra') {
            this.zujian9 = item.objectInfo.split(',');
            counted3++;
          }
        });
        this.zujian8 = [];
        for (var i = 0; i < this.items1.length; i++) {
          this.items2 = this.items1[i].split(':');
          this.zujian8.push({ "value21": this.items2[1], "value22": this.items2[2], 'detailkey': this.items2[0] });
        }
        if (counted1 == 0) {
          this.zujian7 = [''];
        }
        if (counted2 == 0) {
          this.zujian8 = [{ "value21": '', "value22": '', 'detailkey': '' }];
        }
        if (counted3 == 0) {
          this.zujian9 = [''];
        }
        this.files10 = [];
        this.files11 = [];
        this.files12 = [];
        this.files13 = [];
        this.files14 = [];
        this.files15 = [];
        this.files16 = [];
        this.files17 = [];
        this.files18 = [];
        this.zujian_ist2 = this.listPack.productImageList;
        this.zujian_ist3 = this.listPack.productFileList;
        for (var j = 0; j < this.zujian_ist2.length; j++) {
          if (this.zujian_ist2[j].dataResourceName == 'homeDisplayPictures') {
            let name = this.zujian_ist2[j].objectInfo.split('/');
            this.files10.push(name[name.length - 1]);
          } else if (this.zujian_ist2[j].dataResourceName == 'listImages') {
            let name = this.zujian_ist2[j].objectInfo.split('/');
            this.files11.push(name[name.length - 1]);
          } else if (this.zujian_ist2[j].dataResourceName == 'listSpecification') {
            let name = this.zujian_ist2[j].objectInfo.split('/');
            this.files12.push(name[name.length - 1]);
          } else if (this.zujian_ist2[j].dataResourceName == 'listPurchasingContract') {
            let name = this.zujian_ist2[j].objectInfo.split('/');
            this.files13.push(name[name.length - 1]);
          } else if (this.zujian_ist2[j].dataResourceName == 'listVideo') {
            let name = this.zujian_ist2[j].objectInfo.split('/');
            this.files14.push(name[name.length - 1]);
          } else if (this.zujian_ist2[j].dataResourceName == 'detailsDisplay') {
            let name = this.zujian_ist2[j].objectInfo.split('/');
            this.files15.push(name[name.length - 1]);
          } else if (this.zujian_ist2[j].dataResourceName == 'productDetail') {
            let name = this.zujian_ist2[j].objectInfo.split('/');
            this.files16.push({ "name": name[name.length - 1], "valuedetails": this.zujian_ist2[j].objectInfo.split(':')[1], 'detailkey1': this.zujian_ist2[j].objectInfo.split(':')[0] });
          }
        }
        for (var K = 0; K < this.zujian_ist3.length; K++) {
          if (this.zujian_ist3[K].dataResourceName == 'productDetail') {
            let name = this.zujian_ist3[K].objectInfo.split('/');
            this.files17.push({ "name": name[name.length - 1], "valuedetailes": this.zujian_ist3[K].objectInfo.split(':')[1], 'detailkey2': this.zujian_ist3[K].objectInfo.split(':')[0] });
          } else if (this.zujian_ist3[K].dataResourceName == 'listSpecification') {
            let name = this.zujian_ist3[K].objectInfo.split('/');
            this.files18.push(name[name.length - 1]);
          }
        }
        this.show = col.siteName;
      }
    });

  }
  //站点，组件的收起，展开
  openClick() {
    if (this.vlaueOpen == 'open') {
      this.vlaueOpen = 'down'
    } else {
      this.vlaueOpen = 'open'
    }
  }
}
