import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { SelectItem } from 'primeng/primeng';
import { EventDefinitionBean } from '../bean/event-definition.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//删除
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
@Component({
  selector: 'eventcenter-activity-query',
  templateUrl: './eventcenter-activity-query.component.html',
  styleUrls: ['./eventcenter-activity-query.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class EventcenterActivity implements OnInit {
  tabMesg: any = tableMessage;//列表无数据
  //组件的传入参数
  updateValue: any = [];
  // bean
  eventDefinitionBean: EventDefinitionBean = new EventDefinitionBean();
  //头部标题
  //列表
  dataList: any;
  //总页数
  total: string;
  //每页多少条
  pageSize: string = '10';
  //模态框
  showModel: string;
  headerTitle: string
  display: boolean = false;
  //提示信息
  msgs: Message[] = [];
  //分页控制
  first: number = 0;

  //事件类型
  eventTypeOptions = [{ label: '请选择', value: '' }, { label: '主动服务事件', value: '1' },
  { label: '银行客户业务事件', value: '2' },
  { label: '客户经理任务事件', value: '3' },
  { label: '客户经理自定义事件', value: '4' },
  { label: '银行客户自定义事件', value: '5' }
  ];

  // 状态类型
  statusOptions = [
    { label: '请选择', value: '' },
    { label: '已生效', value: '1' },
    { label: '未生效', value: '0' }
  ]

  code: any;

  eceventTypeList: any = [];

  ecsstatusList: any = [];

  isdisabled: boolean = true;

  constructor(
    public router: Router,
    public cusOperationHttpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {

    this.codeValues() //调用方法，获取全部码值

    this.eceventTypeList = this.code['eceventTypeList'];

    this.ecsstatusList = this.code['ecsstatusList'];

    debugger;


  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  ngOnInit() {
    this.eventDefinitionBean.pageSize = 10;
    this.eventDefinitionBean.pageNum = 1;
    this.eventDefinitionBean.status = '1';
   
    this.first = 0;
    this.queryClick();
  }

  // 重置
  resetClick() {
    this.eventDefinitionBean = new EventDefinitionBean();
  }

  //查询始终第一页
  querySecle() {
    this.eventDefinitionBean.pageSize = 10;
    this.eventDefinitionBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  

  //查询
  queryClick() {


    let param: EventDefinitionBean = new EventDefinitionBean();

    if (this.eventDefinitionBean.eventId) {
      param.eventId = this.eventDefinitionBean.eventId;
    }

    if (this.eventDefinitionBean.eventName) {
      param.eventName = this.eventDefinitionBean.eventName;
    }
    debugger;
    if (this.eventDefinitionBean.eventType) {
      debugger;
      param.eventType = this.eventDefinitionBean.eventType;
    }

    
     
      param.status = this.eventDefinitionBean.status;
    

    param.pageNum =  this.eventDefinitionBean.pageNum;
    param.pageSize =  this.eventDefinitionBean.pageSize;
    

    this.cusOperationHttpService.queryEventDefinitionList(param).subscribe(data => {
      debugger;
      if (data.returnCode.code == "success") {

        //this.total = data.total;
        debugger;

        this.total = data.totalNum;
        this.dataList = data.eventList;

        debugger;

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {

      debugger;

      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  // 修改
  editClick(item) {
    //操作类型 1:新增 2: 修改 operflag: string = "1";
    this.commfunc.setSessionDataCH('operflag', '2');
    this.commfunc.setSessionDataCH('stepflag', '0');
    this.commfunc.setSessionDataCH('event_eventDefinitionBean', JSON.stringify(item));
    this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/create-eventcenter-activite/ecenter-baseinfo']);

  }

  // 详情
  detailsClick(item) {

    this.commfunc.setSessionDataCH('detail_ec_eventDefinitionBean', JSON.stringify(item));

    this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/eventcenter-activity-detail']);

  }
  //详情的回调
  detailsCall(param) {
    this.display = param;
    // this.queryClick();
  }
  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { "eventId": item.eventId, "status": "2" };

        this.cusOperationHttpService.updateEventDefinitionStatus(param).subscribe(data => {

          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.eventDefinitionBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.eventDefinitionBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.eventDefinitionBean.pageSize;
    //调用查询的方法
    this.queryClick();
    //this.eventDefinitionBean.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id);
  }

  add() {
    // 新增
    this.commfunc.setSessionDataCH('operflag', '1');
    this.commfunc.setSessionDataCH('stepflag', '0');
    this.commfunc.setSessionDataCH('event_eventDefinitionBean', '');
    this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/create-eventcenter-activite/ecenter-baseinfo']);
  }

  //启用

  startUse(item) {

    this.confirmationService.confirm({
      message: '确定要启用该行事件?',
      accept: () => {

        let param = { "eventId": item.eventId, "status": "1" };
        this.cusOperationHttpService.updateEventDefinitionStatus(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '启用成功' }];
            this.queryClick();
          }
          else {
            this.msgs = [];
            data.returnCode.message ? data.returnCode.message : '启用失败';
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
      }
    });
  }
  stopUse(item) {

    this.confirmationService.confirm({
      message: '确定要禁用该事件?',
      accept: () => {

        let param = { "eventId": item.eventId, "status": "0" };
        this.cusOperationHttpService.updateEventDefinitionStatus(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '禁用成功' }];
            this.queryClick();
          }
          else {
            this.msgs = [];
            data.returnCode.message ? data.returnCode.message : '禁用失败';
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
      }
    });
  }
}
