import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';




import { EventcenterActivity } from './eventcenter-activity-query/eventcenter-activity-query.component';



import { EcenterBaseinfoComponent } from './ecenter-baseinfo/ecenter-baseinfo.component';
import { EcenterTriggerruleComponent } from './ecenter-triggerrule/ecenter-triggerrule.component';
import { EcenterEnsureComponent } from './ecenter-ensure/ecenter-ensure.component';
import { EcenterRespComponent } from './ecenter-resp/ecenter-resp.component';

import { CreateActiviteComponent } from './create-eventcenter-activite/create-eventcenter-activite.component';//创建营销活动的整个框架

import { EventcenterActivityDetail } from './eventcenter-activity-detail/eventcenter-activity-detail.component';

//活动
export const routes: Routes = [
    {
        path: '',
        children: [
      
            {
                path: 'create-eventcenter-activite',
                component: CreateActiviteComponent,
                children:[
                    {path: 'ecenter-baseinfo', loadChildren: './ecenter-baseinfo/ecenter-baseinfo.module#EcenterBaseinfoModule' },//基本信息
                    {path: 'ecenter-triggerrule', loadChildren: './ecenter-triggerrule/ecenter-triggerrule.module#EcenterTriggerruleModule' },//触发规则
					{path: 'ecenter-ensure', loadChildren: './ecenter-ensure/ecenter-ensure.module#EcenterEnsureModule' },//确定规则
					{path: 'ecenter-resp', loadChildren: './ecenter-resp/ecenter-resp.module#EcenterRespModule' }//响应规则

                ]
            },
         
            {
                path: 'eventcenter-activity-query',
                component: EventcenterActivity
            },
            {
                path: 'eventcenter-activity-detail',
                component: EventcenterActivityDetail
            }
         
          
           
        ]
    }
]
export const EventcenterActiviteRouting: ModuleWithProviders = RouterModule.forChild(routes);